<template>
  <div class="mapApp">
    <div class="mapArea"  :class="mapHalf">
    </div>
    
  </div>
</template>

<script>
 import $ from "jquery"

export default {
  components: {
  },
//   computed: {
//     ...mapGetters({
//        getLayerInfos: "getGisLayers",      //   
//       getInitMapInfo: "getInitMapInfo"    //    ( center , zoomlevel )
//     })
//     //  Object    Object . //main.js getters 
//   },
  watch: {
    // layers : function(data){
    //     console.log(data);
    // }
    // center : function(e){
    //   this.setCenter(e);
    // }
  },
  props: ['defaultLayer'],
  created: function(){
    //   
    this.setInitInfo();
  },
  mounted: function() {
    this.createMap();
  },
  data: function() {
    return {
      zoomlevel : 7,
      center : [126.570667, 33.450701],
      map:null,
      layers:[],
      layerPopups:[],
      bounds:null,
      regionName : '',
      mapHalf : 'mapHeight100',
      searchResult : null,
      searchLayer : null,
      searchInfo : null
    };
  },
  methods: {
    // ...mapActions({ addLayer: "mapStore/addLayer",  }) ,//main.js action 
    getChild : function(name){    //     
      let vueObj = null;
      for(let child of this.$children){
        if(child.$options._componentTag == name) vueObj = child;
      }
      return vueObj;
    },
    setInitInfo: function(){      //  
      var initInfo = this.getInitMapInfo;
      if(initInfo != null){
        if(initInfo.center != null) this.$data.center = initInfo.center;
        if(initInfo.zoomlevel != null) this.$data.zoomlevel = initInfo.zoomlevel;
        if(initInfo.regionName != null) this.$data.regionName = initInfo.regionName;
      }
    },
    createMap: function() {       //  
      // var mapContainer = this.$el;
      var mapContainer = this.$el.lastChild;
      // console.log(this);
      var mapOption = {
          center: new daum.maps.LatLng(this.$data.center[1], this.$data.center[0]), //  
          disableDoubleClick: true,
          disableDoubleClickZoom: true,
          tileAnimation: true,
          level: this.$data.zoomlevel //   
      };

      //   div      
      this.map = new daum.maps.Map(mapContainer, mapOption);

   //   this.showLayer(this.defaultLayer);
    }

  } //  Object    Object .
};
</script>

<style>
.mapApp{
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  overflow: hidden;
}
.mapArea{
  width: 100%;
  height: 100%;
}
.mapHeight100{
  height: 100%;
}
.mapHeight41{
  height: 41%;
}
.labelRanking{
  top: -30px;
  left: -18px;
  width: 36px;
  height: 25px;
  background:url(/images/map/bg_label.png) no-repeat;
  color:#fff;
  text-align:center;
  font-weight: bold;
  z-index: 100;
  cursor:pointer;
}
</style>



